(function () {
    'use strict';

    angular.module('hAdmin.pages.charts.echarts', []).config(routeConfig);

    /** @ngInject */
    function routeConfig($stateProvider) {
        $stateProvider
            .state('pages.charts.echarts', {
                url: '/echarts',
                templateUrl: 'app/pages/charts/echarts/echarts.html',
                // controller:'eChartsCtrl'
            });
    }

})();
