$templateCache.put("app/pages/forms/editors/codeEditor/codeEditor.html","<div class=\"row\"><div class=\"col-sm-6\"><div class=\"ibox\"><div class=\"ibox-title\"><h5>基本编辑器</h5></div><div class=\"ibox-content\"><p class=\"m-b-lg\"><strong>CodeMirror</strong> 是一个灵活的文本代码编辑器。它是专门用于编辑代码，并附带一些语言模块和插件，实现更先进的编辑功能。</p><textarea id=\"codeEditor-code1\">\n                    <script>\n                        // Code goes here\n\n                        // For demo purpose - animation css script\n                        function animationHover(element, animation) {\n                            element = $(element);\n                            element.hover(\n                                    function () {\n                                        element.addClass(\'animated \' + animation);\n                                    },\n                                    function () {\n                                        //wait for animation to finish before removing classes\n                                        window.setTimeout(function () {\n                                            element.removeClass(\'animated \' + animation);\n                                        }, 2000);\n                                    });\n                        }\n                    </script>\n                </textarea></div></div></div><div class=\"col-sm-6\"><div class=\"ibox\"><div class=\"ibox-title\"><h5>主题示例</h5></div><div class=\"ibox-content\"><p class=\"m-b-lg\"><strong>CodeMirror</strong>提供丰富的API接口和CSS主题，详情请访问 <a href=\"http://codemirror.net/\" target=\"_blank\">http://codemirror.net/</a></p><textarea id=\"codeEditor-code2\">\n                    var SpeechApp = angular.module(\'SpeechApp\', []);\n\n                    function VoiceCtrl($scope) {\n\n                        $scope.said=\'...\';\n\n                        $scope.helloWorld = function() {\n                            $scope.said = \"Hello world!\";\n                        }\n\n                        $scope.commands = {\n                            \'hello (world)\': function() {\n                                if (typeof console !== \"undefined\") console.log(\'hello world!\')\n                                $scope.$apply($scope.helloWorld);\n                            },\n                            \'hey\': function() {\n                                if (typeof console !== \"undefined\") console.log(\'hey!\')\n                                $scope.$apply($scope.helloWorld);\n                            }\n                        };\n\n                        annyang.debug();\n                        annyang.init($scope.commands);\n                        annyang.start();\n                    }\n                </textarea></div></div></div></div>");
